export function getBoletoBancarioContent(source) {
  const firstName = source.owner.firstName;
  const {document:boletoDocument, documentCode:boletoDocumentCode} = source.boletoBancario;

  return `
    <p>Ol ${firstName},</p>


    <p>A sua compra foi iniciada e est aguardando o pagamento via boleto bancrio.</p>
    
    <p id="DR-boletoBancarioInformation">
        <strong>Linha digitvel:</strong> ${boletoDocumentCode}<br/>
        <strong>Link do boleto:</strong> <a href="${boletoDocument}" target="_blank">${boletoDocument}</a>
    </p>
    
    <p>Por favor imprima uma cpia do boleto para efetuar o pagamento ou utilize a linha digitvel acima para pagar via Internet Banking. 
    Caso o pagamento no seja processado em at 5 dias teis, seu pedido ser automaticamente cancelado.</p>
    
    <p>Sugerimos imprimir uma cpia do boleto para seus registros.</p>
    
    <p>Agradecemos a sua compra.</p>
  `;
}